<template>
  <div class="search-container">
    <!--  -->
    <!-- van-search formaction ios-->
    <form action="/" class="search-content">
      <van-search v-model="searchContent" @cancel="$router.back()" @focus="isSearchResultShow=false"
        @search="onSearch(searchContent)" @input="onSearchInput"
        placeholder="" show-action background="#3296fa">
      </van-search>
    </form>
    <!-- / -->
    <!--  -->
    <search-result v-if="isSearchResultShow" :searchContent="searchContent" class="search-result"/>
    <!-- / -->
    <!--  -->
    <van-cell-group v-else-if="searchContent" class="search-suggestions">
      <van-cell @click="onSearch(search)" v-for="(search,index) in searchSuggestions" :key="index" icon="search">
        <div slot="title" v-html="highlight(search)"></div>
      </van-cell>
    </van-cell-group>
    <!-- / -->
    <!--  -->
    <van-cell-group v-else class="search-histories">
      <van-cell center title="">
        <template v-if="isDeleteShow">
          <span @click="searchHistories=[]"></span>
          <span @click="isDeleteShow=false">&nbsp;&nbsp;</span>
        </template>
        <van-icon v-else @click="isDeleteShow=true" slot="right-icon" name="delete" color="#969799" />
      </van-cell>
      <van-cell @click="onHistoriesClick(search,index)" v-for="(search,index) in searchHistories" :key="index" :title="search">
        <!-- v-show -->
        <van-icon v-show="isDeleteShow" slot="right-icon" name="close" color="#969799"/>
      </van-cell>
    </van-cell-group>
    <!-- / -->
  </div>
</template>

<script>
import { debounce } from 'lodash'
import { getSuggestions } from '@/api/search'
import { setItem, getItem } from '@/utils/storage'
import searchResult from './components/search-result'

export default {
  name: 'SearchPage',
  components: {
    searchResult
  },
  data () {
    return {
      searchContent: '', //  
      searchHistories: getItem('search-histories') || [], //   null v-for []
      searchSuggestions: [], // 
      isSearchResultShow: false, // 
      isDeleteShow: false // 
    }
  },
  watch: {
    //  1:  2: 
    searchHistories (val) {
      // console.log(val)
      setItem('search-histories', val) // 
    }
  },
  methods: {
    onHistoriesClick (search, index) {
      //  
      if (this.isDeleteShow) {
        this.searchHistories.splice(index, 1)
      } else {
        // 
        this.onSearch(search)
      }
    },
    onSearch (searchContent) {
      // 1.
      this.searchContent = searchContent
      // 2.
      const searchHistories = this.searchHistories
      const index = searchHistories.indexOf(searchContent)
      if (index !== -1) {
        searchHistories.splice(index)
      }
      this.searchHistories.unshift(searchContent)
      // 3.
      this.isSearchResultShow = true// 
    },
    // 
    highlight (str) {
      const searchContent = this.searchContent
      const reg = RegExp(searchContent, 'gi') // g i
      //     
      return str.replace(reg, `<span style="color:red">${searchContent}</span>`)
    },
    // 
    // onSearchInput: async function (){} Es6
    // debounce  1:  2:  : 1
    onSearchInput: debounce(async function () {
      const searchContent = this.searchContent
      // 1.
      if (!searchContent) {
        return
      }
      const { data } = await getSuggestions(searchContent)
      // 2.
      this.searchSuggestions = data.data.options
      // console.log(data)
      // 3.
    }, 200)
  }
}
</script>

<style scoped lang="less">
.van-search__action{
  background-color: #3296fa;
}
.search-content{
  position: fixed;
  width: 100%;
  height: 54px;
  z-index: 1;
}
.search-result,.search-suggestions,.search-histories{
  padding: 55px 0px 0;
}
</style>
