<template>
  <div class="article-list">
    <van-pull-refresh v-model="isLoading" @refresh="refreshLoadArticleList">
      <van-list @load="onLoadArticleList" v-model="loading" :finished="finished" loading-text="..." finished-text="">
        <van-cell @click="$router.push(`/article/${article.art_id}`)"
        v-for="(article,index) in list" :key="index" :title="article.title" />
      </van-list>
    </van-pull-refresh>
  </div>
</template>

<script>
import { getArticleChannels } from '@/api/article'

export default {
  name: 'ArticleListPage',
  props: {
    channel: {
      type: Object, // 
      required: true // 
    }
  },
  data () {
    return {
      list: [],
      loading: false,
      finished: false,
      imestamp: null,
      isLoading: false
    }
  },
  methods: {
    async onLoadArticleList () {
      // 1.
      const { data } = await getArticleChannels({
        channel_id: this.channel.id, // ID
        timestamp: this.imestamp || Date.now(), // 
        with_top: 1 // 1-0-
      })
      // console.log(data)
      // 2.
      const { results } = data.data
      this.list.push(...results)
      // 3.
      this.loading = false
      // 4.
      if (results.length) {
        this.imestamp = data.data.pre_timestamp
      } else {
        this.finished = true
      }
    },
    async refreshLoadArticleList () {
      // 1.
      const { data } = await getArticleChannels({
        channel_id: this.channel.id,
        timestamp: Date.now(), // 
        with_top: 1
      })
      // 2.
      const { results } = data.data
      this.list.unshift(...results)
      // 3.loading
      this.isLoading = false
      // 4.
      this.$toast(`${results.length}`)
    }
  },
  created () {
    this.onLoadArticleList()
  }
}
</script>

