/**
 * 
*/

import request from '@/utils/request'

// 
export const getUserArticles = (userId, params) => {
  return request({
    params,
    method: 'GET',
    url: `/app/v1_0/users/${userId}/articles`
  })
}

// 
export const getArticleChannels = params => {
  return request({
    params,
    method: 'GET',
    url: '/app/v1_1/articles'
  })
}

// 
export const getArticleDetails = articleId => {
  return request({
    method: 'GET',
    url: `/app/v1_0/articles/${articleId}`
  })
}

// 
export const addCollect = target => {
  return request({
    method: 'POST',
    url: '/app/v1_0/article/collections',
    data: {
      target
    }
  })
}

// 
export const deleteCollect = targetId => {
  return request({
    method: 'DELETE',
    url: `/app/v1_0/article/collections/${targetId}`
  })
}

// 
export const addLike = (target) => {
  return request({
    method: 'POST',
    url: '/app/v1_0/article/likings',
    data: {
      target
    }
  })
}

// 
export const deleteLike = targetId => {
  return request({
    method: 'DELETE',
    url: `/app/v1_0/article/likings/${targetId}`
  })
}

// 
export const getCurrentUserArticle = params => {
  return request({
    method: 'GET',
    url: '/app/v1_0/user/articles',
    params
  })
}

// 
export const getUserArticleCollect = params => {
  return request({
    method: 'GET',
    url: '/app/v1_0/article/collections',
    params
  })
}

// 
export const getUserArticleHistory = params => {
  return request({
    method: 'GET',
    url: '/app/v1_0/user/histories',
    params
  })
}
