<template>
  <van-row class="login-container">
    <!--  -->
    <van-nav-bar title="" />
    <!-- / -->
    <!--  -->
    <!--
    
    1. ValidationObserver 
    2. ValidationProvider  input
        name 
        rules 
      :
       rules="required"
      : rules="requires|mobile" 
      v-slot="{errors}"
    -->
    <ValidationObserver ref="myForm">
      <!-- ValidationObserver immediate -->
      <ValidationProvider immediate name="" rules="required|mobile">
        <van-field v-model="userInfo.mobile" placeholder="">
          <van-icon slot="left-icon" class-prefix="icon" name="yidongmobile216" />
        </van-field>
      </ValidationProvider>
      <ValidationProvider immediate name="" rules="required|code">
        <van-field v-model="userInfo.code" placeholder="" class="verification-code">
          <van-icon slot="left-icon" class-prefix="icon" name="icon--" />
          <van-button @click="sendSmsCode" v-if="!isCountDownShow" round size="small" type="primary" slot="button">
            
          </van-button>
          <van-count-down @finish="isCountDownShow=false" v-else format="ss s" :time="1000*60" slot="button"/>
        </van-field>
      </ValidationProvider>
    </ValidationObserver>
    <!-- / -->
    <van-row class="loginBtn">
      <van-button type="info" @click="userLogin"></van-button>
    </van-row>
  </van-row>
</template>

<script>
import { userLogin, getSmsCode } from '@/api/user'
import { validate } from 'vee-validate'

export default {
  name: 'LoginPage',
  data () {
    return {
      isCountDownShow: false, // 
      userInfo: {
        mobile: '', // 
        code: '' // 
      }
    }
  },
  methods: {
    async sendSmsCode () {
      const { mobile } = this.userInfo
      // 1.
      // 1  2  3   name
      //  { valid,errors,... } valid    true  false errors  
      const validateResult = await validate(mobile, 'required|mobile', {
        name: ''
      })
      if (!validateResult.valid) {
        this.$toast(validateResult.errors[0])
        return
      }
      try {
        // 2.
        // resthen
        // const res = await getSmsCode(mobile)
        await getSmsCode(mobile)
        // console.log(res)
        // 3.
        this.isCountDownShow = true
      } catch (error) {
        // console.log(error)
        if (error.response.status === 429) {
          this.$toast.fail('')
          return
        }
        this.$toast.fail('')
        // 
        this.isCountDownShow = false
      }
    },
    async userLogin () {
      // 1.
      const userInfo = this.userInfo
      // 2.
      const success = await this.$refs.myForm.validate()
      if (!success) {
        // 
        // console.log('')
        const errors = this.$refs.myForm.errors
        // console.log(errors)
        for (let key in errors) {
          const item = errors[key]
          // console.log(item)
          if (item[0]) {
            this.$toast(item[0])
            //   
            return // 
          }
        }
        return // 
      }
      // 

      //  loading
      this.$toast.loading({
        duration: 0, //  toast
        forbidClick: true, // 
        message: '...'
      })
      // 
      // .clear()
      try {
        // 3.
        const res = await userLogin(userInfo)
        //  token  Vuex 
        this.$store.commit('setUser', res.data.data)
        // console.log('', res)
        // 
        this.$toast.success('')
        //  redirect  
        const redirect = this.$route.query.redirect || '/'
        //   replace  ()
        this.$router.replace(redirect)
      } catch (error) {
        // console.log('', error)
        this.$toast.fail(',')
      }
      // 4.
    }
  }
}
</script>

<style scoped lang="less">
.login-container {
  .verification-code {
    height: 47px;
    display: flex;
    align-items: center;
    border-top: 1px solid rgb(133, 129, 129);
    border-bottom: 1px solid rgb(133, 129, 129);
  }
  .loginBtn {
    padding: 27px 16px;
    .van-button--normal {
      width: 100%;
      border-radius: 5px;
      background-color: #6db4fa;
    }
  }
}
</style>
