<template>
  <div class="chat-container">
    <!--  -->
    <van-nav-bar fixed @click-left="$router.back()" title="" left-arrow />
    <!-- / -->
    <!--  -->
    <van-row class="message-list" ref="messageList">
      <!-- :class="{ class: }"
      true: false-->
      <div class="message-item" :class="{ reverse:item.isMe}" v-for="(item,index) in messages" :key="index">
        <van-image class="avatar" slot="icon" round src="https://img.yzcdn.cn/vant/cat.jpeg" />
        <van-row class="title">
          <span>{{item.msg}}</span>
        </van-row>
      </div>
    </van-row>
    <!-- / -->
    <!--  -->
    <van-cell-group class="send-message">
      <van-field v-model.trim="message" center clearable>
        <van-button @click="onSendMessage" slot="button" size="small" type="primary"></van-button>
      </van-field>
    </van-cell-group>
    <!-- / -->
  </div>
</template>

<script>
import io from 'socket.io-client'
import { getItem, setItem } from '@/utils/storage'

export default {
  name: 'UserChat',
  data () {
    return {
      message: '',
      socket: null, // WebSocket 
      messages: getItem('chat-messages') || [] // 
    }
  },
  methods: {
    onSendMessage () {
      const message = this.message
      if (!message.length) {
        return
      }
      //  
      const data = {
        msg: message, // 
        timestamp: Date.now(), // 
        isMe: true // isMe 
      }
      // 
      // socket.emit('',) 
      this.socket.emit('message', data)
      // 
      this.messages.push(data)
      // 
      this.message = ''
    },
    toBottom () {
      // listContainer.scrollTop 
      // listContainer.scrollTop = xxx 
      // ref dom
      //  OffsetTop DOM OffsetTop
      //  .OffsetTop $elDOM
      // this.$refs..$el
      const messageList = this.$refs.messageList.$el
      messageList.scrollTop = messageList.scrollHeight
    }
  },
  watch: {
    //  
    // 
    // 1
    // 2
    messages (value) {
      //  
      setItem('chat-messages', value)
      // 
      // $nextTick()  dom dom
      //  Dom 
      //  Api ?Dom
      //  Vue Api: $nextTick
      this.$nextTick(() => {
        this.toBottom()
      })
    }
  },
  created () {
    // WebSocket  socket 
    //  WebSocket  axios
    const socket = io('http://ttapi.research.itcast.cn') // const socket = io("")
    //  socket  data  methods 
    this.socket = socket
    //  connect 
    socket.on('connect', function () {
      console.log('')
    })

    //     
    // window.socket = socket

    // 
    // socket.on(''data=>console.log(data)) 
    // 
    socket.on('message', message => {
      // console.log('message=>', message)
      this.messages.push(message)
    })
  }
}
</script>

