  display: flex;
  align-items: flex-start;
  padding: 15px;
  .avatar {
    width: 30px;
    height: 30px;
    margin-right: 10px;
  }
  .info {
    display: flex;
    flex-direction: column;
    .content {
      padding: 4px 0 10px;
    }
    .date {
      margin-right: 10px;
    }
  }
  .like-container {
    width: 30px;
    font-size: 13px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}
</style>
