<template>
  <div class="user-profile">
    <van-nav-bar title="">
      <van-icon @click="$router.back()" slot="left" name="arrow-left" />
    </van-nav-bar>
    <van-cell-group>
      <van-cell title="" is-link>
        <van-image @click="onSelectFile" :src="UserProfile.photo" class="avatar" round fit="cover" />
        <input @change="onFileChange" ref="file" hidden type="file" />
      </van-cell>
      <van-cell @click="isEditNameShow = true" :value="UserProfile.name" title="" is-link />
      <van-cell @click="isEditGenderShow = true"
      :value="UserProfile.gender === 0 ? '' : ''" title="" is-link />
      <van-cell @click="isEditBirthdayShow = true" :value="UserProfile.birthday" title="" is-link />
    </van-cell-group>
    <!--  -->
    <van-image-preview @close="$refs.file.value=''" v-model="isPreviewShow" :images="images"/>
    <!-- / -->
    <!--  -->
    <van-popup v-model="isEditNameShow" position="bottom" :style="{ height: '22%' }">
      <van-nav-bar @click-left="isEditNameShow = false" @click-right="onUpdateName"
      title="" left-text="" right-text="" />
      <!-- field  input   value value:-->
      <van-field @input="inputName = $event" :value="UserProfile.name"
      rows="2" autosize type="textarea" maxlength="20" placeholder="" show-word-limit />
    </van-popup>
    <!-- / -->
    <!--  -->
    <van-action-sheet @select="onGenderSelect" @cancel="isEditGenderShow = false"
    v-model="isEditGenderShow" :actions="actions" cancel-text=""/>
    <!-- / -->
    <!--  -->
    <van-popup v-model="isEditBirthdayShow" position="bottom" >
      <van-datetime-picker @confirm="onUpdateBirthday" @cancel="isEditBirthdayShow = false"
      :value="currentDate" type="date" :min-date="minDate" :max-date="maxDate" />
    </van-popup>
    <!-- / -->
  </div>
</template>

<script>
import moment from 'moment'
import { ImagePreview } from 'vant'
import { getUserProfile, updateUserPhoto, updateUserProfile } from '@/api/user'

export default {
  name: 'UserProfile',
  data () {
    return {
      UserProfile: {},
      images: [], // 
      isPreviewShow: false, // 
      isEditNameShow: false, // 
      inputName: '', // 
      isEditGenderShow: false,
      isEditBirthdayShow: false, //  ActionSheet 
      actions: [
        // name  value 
        { name: '', value: 0 },
        { name: '', value: 1 }
      ],
      minDate: new Date(1970, 1, 1),
      maxDate: new Date()
    }
  },
  methods: {
    async onUpdateBirthday (value) {
      // console.log(value)
      //  moment 
      const date = moment(value).format('YYYY-MM-DD')
      // 
      await this.updateUserProfile('birthday', date)
      // 
      this.UserProfile.birthday = date
      // 
      this.isEditBirthdayShow = false
    },
    async onGenderSelect (data) {
      // console.log(data)
      // 
      await this.updateUserProfile('gender', data.value)
      // 
      this.UserProfile.gender = data.value
      // 
      this.isEditGenderShow = false
    },
    async onUpdateName () {
      // 
      await this.updateUserProfile('name', this.inputName)
      // 
      this.UserProfile.name = this.inputName
      // 
      this.isEditNameShow = false
    },
    // field 
    // value:
    async updateUserProfile (field, value) {
      this.$toast.loading({
        duration: 0, //  toast
        message: '...',
        forbidClick: true // 
      })
      try {
        await updateUserProfile({
          [field]: value //  
        })
        this.$toast.success('')
      } catch (error) {
        // console.log(error)
        this.$toast.fail('')
      }
    },
    onFileChange () {
      // 
      // 2.  window.URL.createObjectURL(file) 
      const fileData = URL.createObjectURL(this.file.files[0])
      this.images = [fileData] // 
      this.isPreviewShow = true
      ImagePreview({
        images: [fileData], // 
        onClose: this.updateAvatar
      })
    },
    async updateAvatar () {
      try {
        await this.$dialog.confirm({
          title: '',
          message: ''
        })
        //  Content-Type  application/json  data  {}
        //  Content-Type  multipart/form-data  data  FormData 
        //  Content-Type  application/json
        // Content-Type  multipart/form-data
        //  FormData 
        this.$toast.loading({
          duration: 0, //  toast
          message: '...',
          forbidClick: true // 
        })
        // 1.  file  input 
        const fileObj = this.file.files[0]
        // 
        const fd = new FormData()
        // formData.append('', )
        fd.append('photo', fileObj)
        // 
        const { data } = await updateUserPhoto(fd)
        // 
        this.UserProfile.photo = data.data.photo
        // 
        this.isPreviewShow = false
        // 
        this.$toast.success('')
      } catch (error) {
        // console.log(error)
        this.isPreviewShow = false
        this.$toast.fail('')
      }
    },
    onSelectFile () {
      //  input 
      this.file.click()
    },
    async loadUserProfile () {
      try {
        const { data } = await getUserProfile()
        // console.log(data)
        this.UserProfile = data.data
      } catch (error) {
        // console.log(error)
        this.$toast.fail('')
      }
    }
  },
  computed: {
    // 
    //  ref  this.$refs['file]
    //  
    file () {
      return this.$refs['file']
    },
    currentDate () {
      // js  vant 
      return new Date(this.UserProfile.birthday)
    }
  },
  created () {
    this.loadUserProfile()
  }
}
</script>

<style scoped lang="less">
.user-profile {
  .avatar {
    width: 30px;
    height: 30px;
  }
  .van-popup{
    background-color: #333;
    /deep/.van-nav-bar{
      background: #fff;
      .van-nav-bar__title{
        color: #333
      }
    }
     .van-cell{
        padding: 10px;
        background-color: #ededed;
        /deep/.van-cell__value{
        background-color: #fff;
        }
      }
  }
}
</style>
