<template>
  <div class="article-comment">
      <!--  -->
      <van-list v-model="loading" :finished="finished" finished-text="" @load="onLoadArticleComments">
          <comment-item @click-reply="$emit('click-reply',$event)"
          v-for="(item,index) in list" :key="index" :comment="item" />
      </van-list>
      <!-- / -->
  </div>
</template>

<script>
import commentItem from './comment-item'
import { getComments } from '@/api/comment'

export default {
  name: 'ArticleComment',
  components: {
    commentItem
  },
  props: {
    articleId: {
      type: [Number, String, Object],
      required: true
    }
  },
  data () {
    return {
      list: [],
      loading: false,
      finished: false,
      offset: null,
      limit: 20
    }
  },
  methods: {
    // 1.
    async onLoadArticleComments () {
      const { data } = await getComments({
        type: 'a', // a-(article) c-(comment)
        source: this.articleId, // id idid
        offset: this.offset, //  id id 
        limit: this.limit //  
      })
      // console.log(data)
      const { results } = data.data
      // 2.
      this.list.push(...results)
      // 3.loading
      this.loading = false
      // 4.
      if (results.length) {
        this.offset = data.data.last_id
      } else {
        this.finished = true
      }
    }
  }
}
</script>

