//  

// :  
import Login from '@/views/login'
import UserProfile from '@/views/user-profile'
import UserChat from '@/views/user-chat'
import UserArticle from '@/views/user-article'
import User from '@/views/user'
import Search from '@/views/search'
import Article from '@/views/article'
import TabBar from '@/views/tab-bar'
import Home from '@/views/home'
import Question from '@/views/question'
import Video from '@/views/video'
import My from '@/views/my'

Vue.use(VueRouter)

// 
const routes = [
  // 
  {
    name: 'login',
    path: '/login',
    // component: () => import('@/views/login')
    component: Login
  },
  {
    path: '/',
    // component: () => import('@/views/tab-bar'),
    component: TabBar,
    children: [
      // 
      {
        name: 'home',
        path: '', //   home
        // component: () => import('@/views/home')
        component: Home
      },
      // 
      {
        name: 'question',
        path: '/question',
        // component: () => import('@/views/question')
        component: Question
      },
      // 
      {
        name: 'video',
        path: '/video',
        // component: () => import('@/views/video')
        component: Video
      },
      // 
      {
        name: 'my',
        path: '/my',
        // component: () => import('@/views/my')
        component: My
      }
    ]
  },
  //   path: '/user/:userId'  
  //  
  {
    name: 'user-profile',
    path: '/user/profile',
    // component: () => import('@/views/user-profile')
    component: UserProfile
  },
  // 
  {
    name: 'user-chat',
    path: '/user/chat',
    // component: () => import('@/views/user-chat')
    component: UserChat
  },
  // 
  {
    name: 'user',
    path: '/user/:userId', // 
    // component: () => import('@/views/user')
    component: User
  },
  // 
  {
    name: 'search',
    path: '/search',
    // component: () => import('@/views/search')
    component: Search
  },
  //  
  {
    name: 'article',
    path: '/article/:articleId',
    // component: () => import('@/views/article'),
    component: Article,
    props: true // props 
    //   1:this.$router.params.id
    // 2. propstrue propsid
    // https://router.vuejs.org/zh/guide/essentials/passing-props.html
  },
  // //
  {
    name: 'my-article',
    path: '/my-article/:type?', //  :type?  /my-article  /my-article/a  /my-article/b
    // component: () => import('@/views/user-article'),
    component: UserArticle,
    props: true
  }
]

const router = new VueRouter({
  routes
})

export default router
