/**
 * axios
 */

import axios from 'axios'
import jsonBig from 'json-bigint'

//  
// store  this.$store 
import store from '@/store/index.js'
import router from '@/router'

// axios.create  axios 
const request = axios.create({
  baseURL: 'http://ttapi.research.itcast.cn/' // 
})

// axios  API
// data 
request.defaults.transformResponse = [function (data) {
  //  json-bigint  JSON 
  //  JSON.parse, JS 
  //     .toString()  id
  //  
  //   catch 
  try {
    return jsonBig.parse(data)
    //  JSON.parse(data)
  } catch (error) {
    // console.log(' JS ', error)
    return {}
  }
}]

// 
request.interceptors.request.use(function (config) {
  // config  config 
  const { user } = store.state
  // console.log(user)
  if (user) {
    // console.log(config)
    //  token
    // config.headers 
    // Authorization   Bearer token
    config.headers.Authorization = `Bearer ${user.token}`
  }
  return config
}, function (error) {
  return Promise.reject(error)
})

// 
request.interceptors.response.use(function (response) {
  return response
}, async function (error) {
  // token
  //  token  token  401
  //  401  
  if (error.response && error.response.status === 401) {
    // 1. refresh_token 
    const user = store.state.user
    if (!user || !user.refresh_token) {
      redirectLogin()
      return
    }
    // 2. token
    try {
      //  axios  token?
      //  token  refresh_token   Authorization
      //  request   token
      //  request  request  token  refresh_token
      //  token   refresh_token  token   token 
      //  request  token   request  token 
      //   token  
      //  request  token 
      const { data } = await axios({
        method: 'PUT',
        url: 'http://ttapi.research.itcast.cn/app/v1_0/authorizations',
        Headers: {
          Authorization: `Bearer ${user.refresh_token}`
        }
      })
      // 3.token  token
      // console.log(data)

      // user.token = data.data.token
      // store.commit('setUser', user)

      // store.commit  mutation
      store.commit('setUser', {
        ...user, //  user:{ id,token,refresh_token }
        token: data.data.token // token
      })
      // 4.
      // error.config   method url
      //   error.config 
      // console.log(error.config)
      //  token
      // :  request  
      // :  return  
      return request(error.config)
    } catch (error) {
      // console.log('token', error)
      redirectLogin()
    }
  }
  return Promise.reject(error)
})

function redirectLogin () {
  router.push({
    name: 'login',
    // query  key=calue&key=value  url 
    // query 
    // query  ?key=value 
    query: {
      //  
      // router.currentRoute   this.$route
      // fullPath
      // redirect  
      redirect: router.currentRoute.fullPath
    }
  })
}

export default request
