<template>
  <div class="user-articles">
    <!--  -->
    <van-nav-bar fixed @click-left="$router.replace('/my')" title="//" left-arrow />
    <!-- / -->
    <!--  -->
    <van-tabs v-model="active">
      <van-tab title="">
        <collect-article />
      </van-tab>
      <van-tab title="">
        <history-article />
      </van-tab>
      <van-tab title="">
        <user-article />
      </van-tab>
