<template>
  <div class="comment-reply">
    <van-nav-bar :title="`${comment.reply_count}`">
      <van-icon @click="$emit('click-close')" slot="left" name="cross" />
    </van-nav-bar>
    <comment-item :comment="comment" />
    <van-cell title="" />
    <van-list @load="onLoadComment" v-model="loading" :finished="finished" finished-text="">
      <comment-item :comment="comment" v-for="(comment,index) in list" :key="index" />
    </van-list>
    <van-row class="reply-footer">
      <van-button @click="isPostReplyShow=true" class="write-btn" type="default" round size="small"></van-button>
      <van-icon name="good-job" color="#e5645f"/>
    </van-row>
    <van-popup v-model="isPostReplyShow" position="bottom" :style="{height:'18%'}">
      <post-comment @click-post="onPostReplyComment" v-model="postMessage" />
    </van-popup>
  </div>
</template>

<script>
import postComment from './post-comment'
import commentItem from './comment-item'
import { getComments, addComments } from '@/api/comment'
export default {
  name: 'CommentReply',
  components: {
    commentItem,
    postComment
  },
  props: {
    comment: {
      type: Object,
      required: true
    },
    articleId: {
      type: [Number, String, Object],
      required: true
    }
  },
  data () {
    return {
      list: [],
      loading: false,
      finished: false,
      offset: null,
      limit: 20,
      isPostReplyShow: false,
      postMessage: ''
    }
  },
  methods: {
    async onPostReplyComment () {
      this.$toast.loading({
        duration: 0, //  toast
        message: '...',
        forbidClick: true // 
      })
      try {
        const { data } = await addComments({
          target: this.comment.com_id.toString(), // idid id
          content: this.postMessage, // 
          art_id: this.articleId // id   id  
        })
        // console.log(data)
        // 
        this.list.unshift(data.data.new_obj)
        // 
        this.comment.reply_count++
        this.$toast.success('')
        // 
        this.postMessage = ''
        // 
        this.isPostReplyShow = false
      } catch (error) {
        // console.log(error)
        this.$toast.fail('')
        this.postMessage = ''
      }
    },
    async onLoadComment () {
      // 1.
      const { data } = await getComments({
        type: 'c', // a-(article) c-(comment)
        source: this.comment.com_id.toString(), // id idid
        offset: this.offset, //  id id 
        limit: this.limit //  
      })
      // console.log(data)
      const { results } = data.data
      // 2.
      this.list.push(...results)
      // 3.loading
      this.loading = false
      // 4.
      if (results.length) {
        this.offset = data.data.last_id
      } else {
        this.finished = true
      }
    }
  }
}
</script>

<style scoped lang="less">
.reply-footer {
  height: 44px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  border-top: 1px solid #d8d8d8;
  background-color: #fff;
  .write-btn {
    width: 60%;
    height: 30px;
  }
}
</style>
