import Vue from 'vue'
import Vuex from 'vuex'

import { getItem, setItem } from '@/utils/storage.js'
import decodeJwt from 'jwt-decode'

Vue.use(Vuex)

const USER_KEY = 'user'

export default new Vuex.Store({
  state: {
    user: getItem(USER_KEY), // (token)
    cachePages: ['TabBar']
  },
  mutations: {
    // 
    //   token  
    setUser (state, data) {
      if (data && data.token) {
        // console.log(decodeJwt(data.token))  Jwttoken  id
        data.id = decodeJwt(data.token).user_id // data.id id
      }
      state.user = data
      //    
      setItem(USER_KEY, state.user)
    },
    // 
    addCachePage (state, name) {
      if (!state.cachePages.includes(name)) {
        state.cachePages.push(name)
      }
    },
    // 
    removeCachePage (state, name) {
      const index = state.cachePages.indexOf(name)
      if (index !== -1) {
        state.cachePages.splice(index)
      }
    }
  },
  actions: {
  },
  modules: {
  }
})
