<template>
  <div class="search-result">
      <van-list @load="onloadSearchResult" v-model="loading" :finished="finished" finished-text="">
        <van-cell @click="$router.push(`/article/${search.art_id}`)"
        v-for="(search,index) in searchResult" :key="index" :title="search.title"/>
      </van-list>
  </div>
</template>

<script>
import { getSearchResult } from '@/api/search'

export default {
  name: 'SearchResult',
  props: {
    searchContent: {
      type: String,
      required: true
    }
  },
  data () {
    return {
      searchResult: [], // 
      loading: false,
      finished: false,
      page: 1, //  1
      per_page: null // 
    }
  },
  methods: {
    async onloadSearchResult () {
      // 1.
      const { data } = await getSearchResult({
        page: this.page,
        per_page: this.per_page,
        q: this.searchContent // 
      })
      // 2.
      // console.log(data)
      const { results } = data.data
      this.searchResult.push(...results)
      // 3.
      this.loading = false
      // 4.
      if (results.length) {
        this.page++
      } else {
        this.finished = true
      }
      // 5.
    }
  }
}
</script>

